$(window).on('load', function(){
	var filterItem = $('.filter__button');
	var filterActiveClass = 'filter__button--active';

	var articlesFilter = $('.articles-page__articles').isotope({
		itemSelector: '.articles-page__item',
		layoutMode: 'packery',
		packery: {
			gutter: 0,
		},
	});

	var projectsFilter = $('.projects-page__projects').isotope({
		itemSelector: '.projects-page__item',
		layoutMode: 'packery',
		packery: {
			gutter: 16,
		},
	});

	filterItem.on('click', function() {
		var filterValue = $(this).attr('data-filter');

		$(this).addClass(filterActiveClass).siblings().removeClass(filterActiveClass);
		articlesFilter.isotope({ filter: filterValue });
		projectsFilter.isotope({ filter: filterValue });
	});
});
$(window).on('load', function(){
	var contactsTabs = $('.contacts__tabs button');
	var contactsContent = $('.contacts__content-item');
	var softwareTabs = $('.software__tabs button');
	var softwareContent = $('.software__content-item');
	var productTabs = $('.product__tabs button');
	var productContent = $('.product__content-item');

	function setTabs(tabs, content) {
		tabs.click(function () {
			if (!$(this).hasClass('active')) {
				tabs.removeClass('active');
				$(this).addClass('active');
				content.hide().eq($(this).index()).fadeIn(300);
			}
		});
	}

	setTabs(contactsTabs, contactsContent);
	setTabs(softwareTabs, softwareContent);
	setTabs(productTabs, productContent);
});
function jsImgMapUpdate(){
	$('.js-img-map__inner').each(function(){
		var inner=$(this);
		var innerW=inner.width();
		var innerL=inner.offset().left;
		var bodyW=$('body').width();
		//var imgMap=$(this).closest('.js-img-map');
		//console.log(innerL);
		//200(max-w)+11(отступ от центра точки)=211
		//211+4(погрешность)=215
		var maxW=215;
		var items=inner.find('.img-map-point');
	//	if(bodyW-(innerW+innerL)<=maxW){
		if(window.matchMedia("(min-width: 601px)").matches){
			items.each(function(){
				$(this).removeClass('img-map-point--centred');
				if(bodyW-maxW<=$(this).offset().left){//если крайняя левая точка настолько справа, что это всё не влезет, то
					$(this).addClass('img-map-point--left');
				}
				else{
					$(this).removeClass('img-map-point--left');
				}

			});
			items.each(function(){
				if(bodyW-maxW<=$(this).offset().left){//если крайняя левая точка настолько справа, что это всё не влезет, то
					$(this).addClass('img-map-point--left');
				}
				else{
					$(this).removeClass('img-map-point--left');
				}

			});
		}
		else{
			items.each(function(){
				if($(this).offset().left<=100){
					//в левой части экрана точка
						$(this).removeClass('img-map-point--centred').removeClass('img-map-point--left');
				}
				else{
					if(bodyW-100>$(this).offset().left){
						//в центральной части экрана точка
						$(this).removeClass('img-map-point--left').addClass('img-map-point--centred');
					}
					else{
						//в правой части экрана точка
						$(this).removeClass('img-map-point--centred').addClass('img-map-point--left');

					}
				}

			});

		}


	//	}
	});
}

$(window).on('load', function() {
	jsImgMapUpdate();

	$(window).on('resize', function() {
		jsImgMapUpdate();
	});
	//t r  <150
	//t 100% - left%<150px{left: auto; right: 0;}
});
